<template>
  <div class="luck-box">
    <div class="wheel">
      <canvas class="item" id="wheelCanvas" width="412" height="412"></canvas>
      <img class="pointer" src="./img/wheel-pointer.png" @click="OnRotate"/>
    </div>
  </div>
</template>

<script>
import "./js/jquery-2.1.4.js";
import "./js/jQueryRotate.js";
export default {
  data() {
    return {
      turnWheel: {
        rewardNames: this.rewardNames, //
        // colors: this.colors, //
        outsideRadius: 180, //
        textRadius: 140, //
        insideRadius: 80, //
        startAngle: 0, //

        bRotate: false //false:;ture:
      },
      tip: ""
    };
  },
  watch:{
    rewardNames(newVal){
      this.turnWheel.rewardNames = this.rewardNames
      this.drawWheelCanvas()
    }
  },
  props: {
    rewardNames: {
      default: () => [
        "",
        "",
        "",
        "",
        ""
      ]
    }
  },
  mounted() {
    this.drawWheelCanvas();
  },
  methods: {
    // item:0; tip ,count ;
    rotateFunc(item = 0, tip, count, callback) {
      let _this = this;
      let _tip = "";
      // 
      var baseAngle = 360 / count;
      //  == 270 -  *  - ()
      var angles = 360 * 3 / 4 - item * baseAngle - baseAngle / 2; // 0
      $("#wheelCanvas").stopRotate();
      // jqueryrotate 
      // 
      $("#wheelCanvas").rotate({
        angle: 0, //
        animateTo: angles + 360 * 5, // 5
        duration: 8000, //animateTo
        callback: function() {
          // 
          callback(tip);
          _this.turnWheel.bRotate = !_this.turnWheel.bRotate;
        }
      });
    },
    //
    OnRotate() {
      let _this = this;
      // 
      if (_this.turnWheel.bRotate) return;
      _this.turnWheel.bRotate = !_this.turnWheel.bRotate;
      var count = _this.turnWheel.rewardNames.length;
      console.log("" + count);
      // 
      // ()
      // var GiftId = ""; Id
      // var item = "";
      // var name = "";
      var item = _this.randomNum(0, count - 1);
      // 
      _this.rotateFunc(item, _this.turnWheel.rewardNames[item], count, function(
        _tip
      ) {
        //,
        _this.tip = _tip;
        _this.$emit("OnClick", _tip);
      });
    },

    randomNum(n, m) {
      /* nmn<= random <=m */
      /* Math.floor(Math.random()*10);09 */
      var random = Math.floor(Math.random() * (m - n)) + n;
      console.log("rewardNames[" + random + "]");
      return random;
    },
    //
    drawWheelCanvas() {
      let _this = this;
      //canvas
      var canvas = document.getElementById("wheelCanvas");
      // 
      var baseAngle = Math.PI * 2 / _this.turnWheel.rewardNames.length;
      // 
      var ctx = canvas.getContext("2d");
      var canvasW = canvas.width; // 
      var canvasH = canvas.height; // 
      //
      ctx.clearRect(0, 0, canvasW, canvasH);
      //strokeStyle 
      ctx.strokeStyle = "#FFBE04"; // 
      //font 
      ctx.font = "16px Microsoft YaHei";
      // 0
      // 
      var length = _this.turnWheel.rewardNames.length;
      for (var index = 0; index < length; index++) {
        // 
        var angle = _this.turnWheel.startAngle + index * baseAngle;
        // 
        ctx.fillStyle = "#FFF4D7";

        // 
        // -------------------
        ctx.beginPath();
        /*
		 * IOSQuartz2D
		 * context.arc(x,y,r,sAngle,eAngle,counterclockwise);
		 * x :x
		 * y :x
		 * sAngle,eAngle :
		 * counterclockwise : ,False = true = 
		 * */
        ctx.arc(
          canvasW * 0.5,
          canvasH * 0.5,
          _this.turnWheel.outsideRadius,
          angle,
          angle + baseAngle,
          false
        );
        ctx.arc(
          canvasW * 0.5,
          canvasH * 0.5,
          _this.turnWheel.insideRadius,
          angle + baseAngle,
          angle,
          true
        );
        ctx.stroke();
        ctx.fill();
        //Restore00
        ctx.save();

        /*------------*/
        // 
        ctx.fillStyle = "#E5302F";
        var rewardName = _this.turnWheel.rewardNames[index];
        var line_height = 17;
        // translate (0,0) 
        // context.translate(x,y);
        var translateX =
          canvasW * 0.5 +
          Math.cos(angle + baseAngle / 2) * _this.turnWheel.textRadius;
        var translateY =
          canvasH * 0.5 +
          Math.sin(angle + baseAngle / 2) * _this.turnWheel.textRadius;
        ctx.translate(translateX, translateY);
        // rotate
        // angle +  baseAngle / 2   + 90
        ctx.rotate(angle + baseAngle / 2 + Math.PI / 2);
        /** () **/
        // canvas  measureText() 
        // fillText() . fillStyle /
        /*
		 * context.fillText(text,x,y,maxWidth);
		 * ytop
		 * */
        if (rewardName.indexOf("M") > 0) {
          // 
          var rewardNames = rewardName.split("M");
          for (var j = 0; j < rewardNames.length; j++) {
            ctx.font =
              j == 0 ? "bold 20px Microsoft YaHei" : "16px Microsoft YaHei";
            if (j == 0) {
              ctx.fillText(
                rewardNames[j] + "M",
                -ctx.measureText(rewardNames[j] + "M").width / 2,
                j * line_height
              );
            } else {
              ctx.fillText(
                rewardNames[j],
                -ctx.measureText(rewardNames[j]).width / 2,
                j * line_height
              );
            }
          }
        } else if (rewardName.indexOf("M") == -1 && rewardName.length > 6) {
          //
          rewardName =
            rewardName.substring(0, 6) + "||" + rewardName.substring(6);
          var rewardNames = rewardName.split("||");
          for (var j = 0; j < rewardNames.length; j++) {
            ctx.fillText(
              rewardNames[j],
              -ctx.measureText(rewardNames[j]).width / 2,
              j * line_height
            );
          }
        } else {
          //
          ctx.fillText(rewardName, -ctx.measureText(rewardName).width / 2, 0);
        }
        //save()
        ctx.restore();

        /*--------*/
      }
    }
  },
  components: {}
};
</script>
<style scoped>
/*  */
.luck-box {
  display: block;
  padding: 20px;
  /**/
  /* margin: 30px auto; */
}
.wheel {
  display: block;
  width: 30%;
  position: relative;
  margin: 20px auto;
  /**/
  background: url("./img/wheel-bg.png") no-repeat center;
  background-position-y: 1px;
  background-size: contain;
}
.wheel canvas.item {
  width: 100%;
}
.wheel img.pointer {
  position: absolute;
  left: 2px;
  right: 0;
  bottom: 12%;
  top: 0;
  margin: auto;
  width: 35%;
}
</style>



